/* APP */

html {
  background-color: #fafafa;
}

.App {
  margin: 0 auto;
  text-align: center;
  background-color: #fafafa;
  height: 100%;
}


/* ------------------------------------------------------------------------- */
/* --------------------------------------------------------------------- */
/* LOG IN & SIGN IN */

.instaPhone {
  margin-right: 50px;
  margin-top: -50px;
}

.loginmain,
.signupContainer {
  display: flex;
  justify-content: center;
  margin-top: 100px;
  padding: 20px;
}

.formBox {
  height: 380px;
  width: 350px;
  border: 1px solid #dbdbdb;
  background-color: #fff;
  padding: 30px;
}

.signup-box {
  height: 450px;
  width: 350px;
  border: 1px solid #dbdbdb;
  background-color: #fff;
  padding: 30px;
}

.boxOrder {
  display: flex;
  flex-direction: column;
}

.noAccContainer {
  margin-top: 10px;
  height: 50px;
  width: 390px;
  border: 1px solid #dbdbdb;
  background-color: #fff;
  padding: 10px;
}

.signuplink,
.loginInRoute {
  color: rgb(77, 181, 249);

}

.signuplink:hover,
.loginInRoute:hover {
  color: #3f51b5;
}

.boxh1 {
  margin-top: -10px;
}

.instaLogo {
  background-color: #fff;
  width: 175px;
}

.logIn,
.signup {
  width: 258px;
  height: 36px;
  margin: 5px;
  border: 1px solid #dbdbdb;
}

.signinbtn,
.signupbtn,
.loadMoreBtn {
  margin-top: 20px;
  margin-bottom: 10px;
  width: 262px;
  height: 30px;
  background-color: rgb(77, 181, 249);
  color: #fff;
  border-radius: 7px;
  border: none;
}

.signinbtn:hover,
.signupbtn:hover {
  background-color: #3f51b5;
}


.popup {
  cursor: pointer;
  animation: flashing-text 3s ease-in-out infinite;
}

@keyframes flashing-text {
  0% {
    color: transparent;
  }

  50% {
    color: black;
  }

  100% {
    color: transparent;
  }
}

/* ------------------------------------------------------------------------- */
/* ------------------------------------------------------------------------- */

/* POST */
.postContainer {
  /* list-style: none; */
  width: 610px;
  border: 1px solid #dbdbdb;
  margin-top: 25px;
  margin-left: 30px;

}

.center,
.commentBox {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.header {
  height: 60px;
  width: 600px;
  border-bottom: 1px solid #dbdbdb;
  display: flex;
  justify-content: flex-start;
}

.avatarImg {
  min-height: 30px;
  min-width: 30px;
  margin: 10px;
}

.username {
  font-weight: bold;
  margin-top: 20px;
}

.postImg {
  width: 600px;
  height: 600px;
}

.image {
  object-fit: cover;
}


.analytics {
  width: 600px;
  display: flex;
  justify-items: flex-start;
  margin: 10px;
}

.postLove {
  width: 25px;
  background-color: #dbdbdb;
  margin-right: 10px;
}

.postComment {
  width: 23px;
  background-color: #dbdbdb;
  margin-right: 10px;
}

.postShare {
  width: 25px;
  color: #dbdbdb;
  margin-right: 10px;
}

.likes {
  width: 600px;
  display: flex;
  justify-content: flex-start;
}

.commentBox {
  margin-top: 10px;
  height: 56px;
  width: 595px;
}

/* ------------------------------------------------------------------------- */
/* ------------------------------------------------------------------------- */
/* NAVBAR */

.dropdown-menu {
  position: absolute;
  top: 100px;
  right: 20px;
  background-color: #fff;
  padding: 10px 20px;
  width: 200px;
  display: none;
  position: absolute;
  z-index: 1;
  height: 100px;
  border: 1px solid #dbdbdb;
}

.menu-trigger {
  cursor: pointer;
  border-radius: 50%;
  border: 3px solid transparent;
  animation: flashing-border 2s ease-in-out infinite;
}

@keyframes flashing-border {
  0% {
    border-color: transparent;
  }

  50% {
    border-color: #3f51b5;
  }

  100% {
    border-color: transparent;
  }
}

.show {
  display: block;
}

.navContainer {
  height: 55px;
  background-color: #fff;
  border-bottom: 2px solid #dbdbdb;
  display: flex;
  align-items: center;
  justify-content: space-between;

}

.navButton {
  margin: 10px 10px 0 10px;
  background-color: #fff;

}

.navButtonA {

  margin: 1px 10px 10px 10px;
}

.instaLogoNav {
  margin: 10px 0 0 10px;
  width: 100px;
}

navButton:hover {
  background: rgba(0, 0, 0, 0.3);
}

.navSearch {
  height: 30px;
  width: 215px;
  margin-top: 10px;
  border: 1px solid #dbdbdb;
  text-align: center;
  background-color: #fafafa;
  border-radius: 5px;
}

.menu {
  width: 100%;
}

